html,
body,
p,
ol,
ul,
li,
dl,
dt,
dd,
blockquote,
figure,
fieldset,
legend,
textarea,
pre,
iframe,
hr,
h1,
h2,
h3,
h4,
h5,
h6 {
  margin: 0;
  padding: 0;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-size: 100%;
  font-weight: normal;
}

ul {
  list-style: none;
}

button,
input,
select,
textarea {
  margin: 0;
}

html {
  box-sizing: border-box;
}

* {
  box-sizing: inherit;
}

*:before, *:after {
  box-sizing: inherit;
}

img,
embed,
object,
audio,
video {
  height: auto;
  max-width: 100%;
}

iframe {
  border: 0;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

td,
th {
  padding: 0;
  text-align: left;
}

@font-face {
  font-family: DinPro;
  src: url(../fonts/DINPro.otf);
}

html,
body {
  font-family: DinPro, -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue", "Helvetica", "Arial", sans-serif;
}

body {
  display: flex;
  flex-flow: column nowrap;
  min-height: 100vh;
  justify-content: space-between;
}

header {
  flex: 0;
}

main {
  flex: 1;
}

footer {
  flex: 0;
}

header {
  background-color: #333333;
}

main {
  background-color: #e9e1d9;
}

.top-panel {
  display: flex;
  justify-content: space-between;
  font-family: DinPro, -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue", "Helvetica", "Arial", sans-serif;
  height: 30px;
  line-height: 30px;
  padding: 0 13px;
  background-color: #000;
  color: #fbea5e;
  font-size: 21px;
  text-shadow: 1px 1px 1px rgba(251, 234, 94, 0.6);
  text-transform: uppercase;
}

.top-panel .left, .top-panel .right {
  flex: 1;
}

.top-panel .right {
  text-align: right;
}

.top-panel .center {
  flex: 1;
  display: flex;
  justify-content: inherit;
}

.top-panel .center .jackpot-item {
  display: flex;
}

.top-panel .center .jackpot-item .jackpot-name {
  margin-right: 11px;
}

.top-panel .center .jackpot-item .jackpot-counter {
  font-size: 17px;
  position: relative;
}

.top-panel .center .jackpot-item .jackpot-counter .row {
  display: flex;
  overflow: hidden;
  background-image: linear-gradient(top, rgba(200, 200, 200, 0.2) 0%, rgba(200, 200, 200, 0.2) 40%, black 45%, black 55%, black 100%);
  background-color: #222222;
  border-radius: 3px;
}

.top-panel .center .jackpot-item .jackpot-counter .row .jackpot-counter-cell {
  width: 19.5px;
  height: 30px;
  position: relative;
  transform: matrix(1, 0, 0, 1, 0, 0);
  transition: transform 1000ms ease-in-out;
}

.top-panel .center .jackpot-item .jackpot-counter .row .jackpot-counter-cell + .jackpot-counter-cell {
  box-shadow: inset 0.09em 0 0.1em 0px rgba(255, 255, 255, 0.2);
}

.top-panel .center .jackpot-item .jackpot-counter .row .jackpot-counter-cell::before {
  position: absolute;
  content: '9 \a 0 \a 1 \a 2 \a 3 \a 4 \a 5 \a 6 \a 7 \a 8 \a 9 \a 1';
  line-height: 1.1;
  text-align: center;
  transform: matrix(1, 0, 0, 1, 0, -12);
  transition: transform 1000ms ease-in-out;
}

div#carousel {
  perspective: 1200px;
  background: #fff;
  padding-top: 5%;
  font-size: 0;
  margin-bottom: 3rem;
}

figure#spinner {
  transform-style: preserve-3d;
  height: 300px;
  -webkit-transform-origin: 50% 50%;
  transform-origin: 50% 50% -400px;
  transition: 1s;
}

figure#spinner figure {
  background: #333;
  width: 1%;
  max-width: 500px;
  position: absolute;
  left: 50%;
  transform-origin: 50% 50% -40px;
  outline: 1px solid transparent;
  overflow: hidden;
  transition: 1s;
  border: 1px solid #eee;
}

figure#spinner figure.focus {
  width: 26%;
  left: 37%;
  margin-top: -3rem;
}

figure#spinner figure span {
  width: 100%;
  color: #eee;
  height: 20px;
  display: block;
  font-size: 20px;
}

figure#spinner figure:nth-child(1) {
  transform: rotateX(0deg);
}

figure#spinner figure:nth-child(2) {
  transform: rotateX(36deg);
}

figure#spinner figure:nth-child(3) {
  transform: rotateX(72deg);
}

figure#spinner figure:nth-child(4) {
  transform: rotateX(108deg);
}

figure#spinner figure:nth-child(5) {
  transform: rotateX(144deg);
}

figure#spinner figure:nth-child(6) {
  transform: rotateX(180deg);
}

figure#spinner figure:nth-child(7) {
  transform: rotateX(216deg);
}

figure#spinner figure:nth-child(8) {
  transform: rotateX(252deg);
}

figure#spinner figure:nth-child(9) {
  transform: rotateX(288deg);
}

figure#spinner figure:nth-child(10) {
  transform: rotateX(324deg);
}

